<template>
  <div class="firm_detail_content">
    
    <div class="firm_intro" v-html="content">
      
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data(){
      return{
        content: '',
      }
    },
    props:[
      'detaildata'
    ],
    created:function(){

      var info = this.$parent.$data.info;

      //this.init();
      this.content = info.company.content?info.company.content:'';
    },
    methods: {

      /*
      init: function () {
        var _this = this
        function content(){
          _this.content = _this.detaildata.content
        }
        setTimeout(content,1000)
      }*/

    }
  }
</script>

<style media="screen">
  .firm_intro img{
    width: 100%;
    height: auto;
  }
  .firm_intro p{
    font-size: 14px;
    color: #898989;
    line-height: 28px;
    padding: 10px 0;
  }
</style>



